import React, { useEffect } from 'react';
import './App.css';
import HomeWorkForm from './components/HomeWorkForm';
import NavBar from './components/Navbar';
import Footer from './components/Footer';

function App() {
  useEffect(() => {
    const updateBodyHeight = () => {
      document.body.style.height = `${window.innerHeight}px`;
    };
    window.addEventListener('resize', updateBodyHeight);
    updateBodyHeight();

    // ✅ Detect WebView and alert
    const isWebView = /FBAN|FBAV|Instagram|Line|Messenger|WebView/.test(navigator.userAgent);
    if (isWebView) {
      alert("📱 Your browser may not support file downloads. Please open in Chrome or your default browser.");
    }

    return () => {
      window.removeEventListener('resize', updateBodyHeight);
    };
  }, []);

  return (
    <div className="App" style={{ display: "flex", flexDirection: "column", minHeight: "100vh" }}>
      <NavBar />
      <main style={{ flex: 1 }}>
        <HomeWorkForm />
      </main>
      <Footer />
    </div>
  );
}

export default App;
